import React, { Component } from "react";
import Post from '../actions/postURL';
import { Card, CardHeader, CardBody, Form, FormGroup, Label, Input, Button, InputGroup, InputGroupAddon, Tooltip, Popover, PopoverBody } from 'reactstrap';
import copy from 'copy-to-clipboard';

class URLForm extends Component {

    constructor (props) {
        super (props);
        
        this.state = {
            shortLink: '',
            copyTooltipOpen: false,
            copyPopoverOpen: false,
            copied: false
        }

        this.SubmitHandler = this.SubmitHandler.bind (this);
        this.toggleCopyTooltip = this.toggleCopyTooltip.bind (this);
        this.toggleCopyPopover = this.toggleCopyPopover.bind (this);
        this.handleCopy = this.handleCopy.bind (this);
    }

    

    async SubmitHandler (event) {
        const pattern = /^((http|https|ftp):\/\/)/;
        event.preventDefault();
        var url = event.target.userURL.value;
        url = url.toLowerCase();
        if (pattern.test (url)) {
            await Post (url, (err, code) => {
                console.log (this.shortLink);
                this.setState ({
                    shortLink: 'https://shorten-ly.herokuapp.com/' + code['shortCode']
                });
            })
        }
    }

    toggleCopyTooltip = () => {
        this.setState ({
            copyTooltipOpen: !this.state.copyTooltipOpen
        });
    }

    toggleCopyPopover = () => {
        this.setState ({
            copyPopoverOpen: !this.state.copyPopoverOpen
        });
    }

    handleCopy () {
        copy (this.state.shortLink);
        this.setState ({
            copyPopoverOpen: true
        });
    }

    render() {
        return (
            <Card style={{borderColor: '#aaaaaa', opacity: '0.75'}}>
                <CardHeader style={{backgroundColor: '#aaaaaa'}}><strong>Shorten your link here...</strong></CardHeader>
                <CardBody style={{backgroundColor: '#f9f9f9'}}>
                    <Form onSubmit={this.SubmitHandler}>
                        <FormGroup>
                            <Label for="userURL">Your Link</Label>
                            <Input type="url" id= "userURL" name="userURL" placeholder="https://your-link-here.com" />
                        </FormGroup>
                        <FormGroup>
                            <Label for="shortURL">Short Link</Label>
                            <InputGroup id="shortLinkCopyGroup">
                                <Input type="url" name="shortURL" placeholder="https://shorten-ly.com/example"
                                    value={this.state.shortLink} disabled={this.state.shortLink === '' ? true : false} />
                                <InputGroupAddon addonType="append">
                                    <Button id="copyButton" disabled={this.state.shortLink === ''} onClick = {this.handleCopy}>
                                        <span className="fa fa-copy fa-lg"></span>
                                    </Button>
                                    <Tooltip placement="top" isOpen={this.state.copyTooltipOpen} target="copyButton" toggle={this.toggleCopyTooltip}>
                                        Copy
                                    </Tooltip>
                                </InputGroupAddon>
                            </InputGroup>
                            <Popover placement="top" isOpen={this.state.copyPopoverOpen} target="shortLinkCopyGroup">
                                <PopoverBody>
                                    Copied!{' '}<span className="fa fa-times-circle fa-red" onClick={this.toggleCopyPopover}></span>
                                </PopoverBody>
                            </Popover>
                        </FormGroup>
                        <Button color="primary" type="submit" className="align-items-center" block>Shorten</Button>
                    </Form>
                </CardBody>
            </Card>
        );
    }
}

export default URLForm;