import React, { useState } from 'react';
import logo from '../images/logo.png';
import { Navbar, NavbarBrand, NavItem, Nav, Tooltip } from 'reactstrap';
import About from './AboutComponent';

function Header (props) {
    const [HomeTooltipOpen, setHomeTooltipOpen] = useState(false);
    const [AboutTooltipOpen, setAboutTooltipOpen] = useState(false);

    const toggleHomeTooltip = () => setHomeTooltipOpen(!HomeTooltipOpen);
    const toggleAboutTooltip = () => setAboutTooltipOpen(!AboutTooltipOpen);

    return (
        <header className="mb-auto">
            <Navbar dark color="faded">
                <div className="container">
                    <NavbarBrand className="mr-auto" href="" id="homeButton">
                        <img src={logo} height="auto" width="50px" alt="Logo" />
                    </NavbarBrand>
                    <Tooltip placement="bottom" isOpen={HomeTooltipOpen} target="homeButton" toggle={toggleHomeTooltip}>
                        Home
                    </Tooltip>
                    <Nav navbar>
                        <NavItem  id="infoButton">
                            <About />
                            <Tooltip placement="bottom" isOpen={AboutTooltipOpen} target="infoButton" toggle={toggleAboutTooltip}>
                                About Shorten-ly
                            </Tooltip>
                        </NavItem>
                    </Nav>
                </div>
            </Navbar>
        </header>
    );
}

export default Header;