import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody } from 'reactstrap';

function About (props) {
    const [aboutModal, setModal] = useState(false);

    const toggle = () => setModal(!aboutModal);

    return (
        <div>
            <Button color="info" size="sm" onClick={toggle}>
                <span className="fa fa-info-circle fa-lg"></span>
            </Button>
            <Modal isOpen={aboutModal} toggle={toggle}>
                <ModalHeader toggle={toggle}>About Shorten-ly</ModalHeader>
                <ModalBody>
                    <p>
                        This web application is a working protoype of a link-shortening service.
                        Though it completely serves the puropse, it may not be suitable for practical use
                        since the domain-name is itself a stretched string.
                    </p>
                    <hr />
                    <p>
                        Frontend designed and developed by <a href="https://github.com/ekagra25" target="_blank"><strong>Ekagra Midha</strong></a>
                    </p>
                    <p>
                        Backend developed by <a href="https://github.com/tanishq-sharma" target="_blank"><strong>Tanishq Sharma</strong></a>
                    </p>
                    <p>
                        Supported by <a href="https://github.com/shashankshekhar7" target="_blank"><strong>Shashank Shekhar</strong></a>
                    </p>
                </ModalBody>
            </Modal>
        </div>
    );
}

export default About;